<script>
import { deleteSeance } from "./data/api";
export default {
  props: { event: null },
  data: () => ({
    showWarring: false,
    assets_path:
      document.querySelector("meta[name='baseapi']").getAttribute("content") +
      "assets/cours/emploi/",
  }),
  computed: {
    start() {
      return this.parseDate(this.event.start);
    },
    end() {
      return this.parseDate(this.event.end);
    },
    teacher() {
      return this.event.extendedProps.teacher;
    },
    _class() {
      return this.event.extendedProps.class;
    },
    subject() {
      return this.event.extendedProps.subject;
    },
    note() {
      return this.event.extendedProps.note;
    },
    _default() {
      return {
        color: "#eeebff",
        borderColor: "#9f90f4",
        textColor: "#6c59d8",
      };
    },
    _isHour() {
      let start = this.parseDate(this.event.start);
      let end = this.parseDate(this.event.end);
      start = Number(start.slice(0, start.indexOf("h")));
      end = Number(end.slice(0, end.indexOf("h")));
      return end - start == 1;
    },
  },
  mounted() {
    console.log("in event");
  },
  methods: {
    parseDate(date) {
      let strDate = new Date(date).toLocaleTimeString();
      return strDate.slice(0, strDate.lastIndexOf(":")).replace(":", "h");
    },
    deleteEvent(e) {
      e.stopPropagation();
      if (this.event.id) {
        deleteSeance(this.event.id)
          .then((res) => {
            console.log(res);
            this.event.remove();
          })
          .catch((err) => {
            let data = err.response.data;
            this.$notify({
              group: "foo",
              type: "error",
              classes: ".noti",
              title: data.title,
              text: data.msg,
            });
          });
      } else {
        this.event.remove();
      }
    },
    closeWarring(e) {
      e.stopPropagation();
      this.showWarring = false;
    },
  },
};
</script>

