import Vue from 'vue';


export function listGroups() {

    return Vue.axios.get('/admin/cours/get_list_groups');

}

export function getConfig() {

    return Vue.axios.get('/admin/cours/generateur_etd_config');

}

export function listSubjects(group) {


    return Vue.axios.get('/admin/cours/get_list_matieres/' + group);
}

export function listTeachers(group) {

    return Vue.axios.get('/admin/cours/get_list_enseignants/' + group);
}


export function listTeachersMatiere(matiere, classe) {

    return Vue.axios.get('/admin/cours/get_list_enseignants_by_matiere/' + matiere + '/' + classe);
}

export function listClasses() {

    return Vue.axios.get('/admin/cours/get_list_salles');
}


export function listTypeLiens() {

    return Vue.axios.get('/admin/cours/get_list_lien_types');
}


export function getEdts(group) {

    return Vue.axios.get('/admin/cours/get_edts/' + group);
}

export function sendAllEvents() {
}
export function deleteSeance(seance) {
    return Vue.axios.post('/admin/cours/delete_seance/' + seance);
}
export function getSeance(etd) {
    return Vue.axios.get('/admin/cours/get_edt_seance/' + etd);
}
export function chekSeance(data) {
    return Vue.axios.get('/admin/cours/check_seance/', {
        params: data
    });
}


export function setActivateEdt(etd) {
    return Vue.axios.get('/admin/cours/activate/' + etd);
}