    },
    meets: [],
    times: times,
    post_etd_path: document
      .querySelector("meta[name='baseapi']")
      .getAttribute("content"),
    assets_path:
      document.querySelector("meta[name='baseapi']").getAttribute("content") +
      "assets/cours/emploi/",
    shows: {
      subject: true,
      teacher: true,
      class: true,
    },
    days: [
      "Sunday",
      "Lundi",
      "Mardi",
      "Mercredi",
      "Jeudi",
      "Vendredi",
      "Samedi",
    ],
    formData: {
      subject: null,
      matiere: null,
      teacher: null,
      class: null,
      note: null,
      end: null,
      start: new Date(),
      is_remotely: false,
      meet_platform: null,
      url_meet: null,
    },
    selectedEvent: null,
  }),
  watch: {
    "formData.subject": {
      handler(matiere) {
        if (matiere) {
          listTeachersMatiere(matiere.id, this.slectedGroup.id).then((res) => {
            this.modelteachers = res.data;
          });
        } else {
          this.modelteachers = this.teachers;
        }
      },
      deep: true,
    },
  },
  computed: {
    options: function () {
      return {
        defaultDate: new Date(2016, 4, 20),
        locale: "fr",
        firstDay: 1,
        slotDuration: "00:30:00",
        slotLabelInterval: 15,
        snapDuration: "00:15:00",
        events: [],
        allDaySlot: false,
        plugins: [timeGridPlugin, interactionPlugin],
        initialView: "timeGridWeek",
        editable: true,
        droppable: true,
        slotMinTime: "08:00:00",
        slotMaxTime: this.etd_config.slot_max_time || "20:30:00",
        expandRows: true,
        dayHeaderFormat: {
          weekday: "long",
        },
        // businessHours: [
        //   {
        //     daysOfWeek: [4],
        //     startTime: "02:00",
        //     endTime: "18:00",
        //   },
        // ],
        // eventMouseEnter: function (info) {
        //   tippy(info.el, {
        //     content: info.event.title,
        //     interactive: true,
        //     flipOnUpdate: true,
        //   });
        // },
        eventClick: (info) => {
          this.selectedEvent = info.event;
          this.clearInputs().then(() => {
            this.$refs.eventModel.show();
          });
        },
        eventContent: (arg) => {
          let EventComponent = Vue.extend(Event);
          let ev = new EventComponent({
            propsData: { event: arg.event },
          });
          ev.$mount();
          return { domNodes: [ev.$el] };
        },
        eventDrop: (info) => {
          this.chekEmpty(info.event);
        },
        eventReceive: (info) => {
          this.chekEmpty(info.event);
        },
        // dayRender: (dayRenderInfo) => {
        //   console.log(dayRenderInfo);
        // },
        // eventConstraint: {
        //   satrt:  new Date(),
        // },
        // selectAllow: (info) => {
        //   return !this.IsDateHasEvent(info.satrt);
        // },
      };
    },
  },
  async mounted() {
    this.initDataClassesGroupsConfig();
    //window.onbeforeunload = function (event) {
    //event.preventDefault();
    //eturn false;
    //};
  },
  methods: {
    initDataSubjectTeacherPeroides(group) {
      listSubjects(group).then((res) => (this.subjects = res.data));
      listTeachers(group).then((res) => {
        this.teachers = res.data;
        this.modelteachers = res.data;
      });
      getEdts(group).then((res) => {
        this.listeEdts = res.data;
        console.log(this.listEvents());
        if (!this.listEvents().length) {
          if (res.data.length) {
            res.data.forEach((edt) => {
              if (edt.active) {
                this.selectedEtd = edt;
                this.getEvents(this.selectedEtd);
                // break;
              }
            });
          }
        }
      });
    },
    initDataClassesGroupsConfig() {
      getConfig().then((res) => (this.etd_config = res.data));

      listClasses().then((res) => (this.classes = res.data));
      listTypeLiens().then((res) => (this.meets = res.data));

      listGroups()
        .then((res) => {
          this.groups = res.data;

          if (this.getParameterByName("classe")) {
            this.slectedGroup = {
              id: this.getParameterByName("classe"),
              label: this.getParameterByName("classe_label"),
            };
            this.nextGroup = this.slectedGroup;
          } else {
            this.slectedGroup = res.data[0];
            this.nextGroup = res.data[0];
          }
          this.edt_label = this.slectedGroup.label;
        })
        .finally(() => {
          this.init();
          this.initDrag();

          // if duplicate
          let group = JSON.parse(localStorage.getItem("group"));
          if (group) {
            console.log("in group");
            console.log(group);
            this.slectedGroup = group;
            this.edt_label = this.slectedGroup.label;
            this.initDataSubjectTeacherPeroides(group.id);
            localStorage.removeItem("group");
            return;
          } else {
            this.initDataSubjectTeacherPeroides(this.slectedGroup.id);
          }
        });
    },
    changeGroup(group) {
      this.nextGroup = group;
      if (this.listEvents().length) {
        this.$refs.confirmeSave.show();
        return;
      }
      this.ConfimeChangeGroup();
    },
    ConfimeChangeGroup() {
      this.$refs.confirmeSave.hide();
      this.$refs.calendar.getApi().removeAllEvents();
      this.slectedGroup = this.nextGroup;
      this.edt_label = this.slectedGroup.label;
      this.selectedEtd = null;
      this.initDataSubjectTeacherPeroides(this.slectedGroup.id);
    },
    eventstoArray(array) {
      return array.map((val) => {
        return {
          id: val.id,
          start: this.getTime(val.start),
          end: this.getTime(val.end),
          day: val.end.getDay(),
          date: this.getDate(val.end),
          ...val.extendedProps,
        };
      });
    },
    serializEvent(event) {
      return {
        id: event.id,
        start: this.getTime(event.start),
        end: this.getTime(event.end),
        day: event.end.getDay(),
        date: this.getDate(event.end),
        ...event.extendedProps,
      };
    },
    sendEtd() {
      if (!(this.start_interval || this.end_interval)) {
        this.interval_state = false;
        return;
      }

      if (this.selectedEtd) {
        this.$refs.post_form.append(
          this.createElement("etd", this.selectedEtd.id)
        );
      }
      // global data of etd;
      this.$refs.post_form.append(
        this.createElement("classe", this.slectedGroup.id)
      );
      this.$refs.post_form.append(this.createElement("label", this.edt_label));

      this.$refs.post_form.append(
        this.createElement("startdate", this.dateToStr(this.start_interval))
      );

      this.$refs.post_form.append(
        this.createElement("enddate", this.dateToStr(this.end_interval))
      );
      if (this.save_plus) {
        this.$refs.post_form.append(
          this.createElement("interval_plus", this.interval_plus)
        );
        this.$refs.post_form.append(
          this.createElement("type_interval", this.type_interval)
        );
      }

      // etd seance
      this.listEvents().forEach((val) => {
        if (val.subject) {
          this.$refs.post_form.append(
            this.createElement("seances_id[]", val.id)
          );
          this.$refs.post_form.append(this.createElement("jours[]", val.day));
          this.$refs.post_form.append(
            this.createElement("seances_date_debut[]", val.start)
          );
          this.$refs.post_form.append(
            this.createElement("seances_date_fin[]", val.end)
          );
          this.$refs.post_form.append(
            this.createElement("seances_date[]", val.date)
          );
          this.$refs.post_form.append(
            this.createElement("salles[]", val.class ? val.class.id : null)
          );
          this.$refs.post_form.append(
            this.createElement("unites[]", val.subject ? val.subject.id : null)
          );

          this.$refs.post_form.append(
            this.createElement(
              "matieres[]",
              val.matiere ? val.matiere.id : null
            )
          );

          this.$refs.post_form.append(
            this.createElement(
              "enseignants[]",
              val.teacher ? val.teacher.id : null
            )
          );

          if (val.is_remotely)
            this.$refs.post_form.append(
              this.createElement("adistances[]", val.is_remotely)
            );

          if (val.meet_platform)
            this.$refs.post_form.append(
              this.createElement("typeliens[]", val.meet_platform.label)
            );

          this.$refs.post_form.append(
            this.createElement("liens[]", val.url_meet)
          );

          this.$refs.post_form.append(
            this.createElement("remarques[]", val.note ? val.note : "")
          );
        }
      });

      // submit form
      this.$refs.post_form.submit();
    },
    saveEvent2() {
      if (!(this.start_interval || this.end_interval)) {
        this.interval_state = false;
        return;
      }
      sendAllEvents(this.prepareData())
        .then((res) => {
          console.log(res);
          this.$notify({
            group: "foo",
            title: "Note ",
            text: res.data.note,
          });
          setTimeout(() => {
            location.href = res.data.url;
          }, 1000);
        })
        .catch((err) => {
          this.$notify({
            group: "foo",
            title: "Note ",
            text: "Server errors",
          });
          console.log(err);
        });
    },
    prepareData() {
      let formData = new FormData();
      formData.append("class", this.slectedGroup.id);
      formData.append("class", this.slectedGroup.id);
      formData.append("startdate", this.dateToStr(this.start_interval));
      formData.append("enddate", this.dateToStr(this.end_interval));

      this.listEvents().forEach((val) => {
        formData.append("jours[]", val.day);
        formData.append("seances_date_debut[]", val.start);
        formData.append("seances_date_fin[]", val.end);
        formData.append("salles[]", val.class ? val.class.id : null);
        formData.append("matieres[]", val.subject ? val.subject.id : null);
        formData.append("enseignants[]", val.teacher ? val.teacher.id : null);
        if (val.is_remotely) formData.append("distances[]", val.is_remotely);
        formData.append("typeliens[]", val.meet_platform);
        formData.append("liens[]", val.url_meet.label);
        formData.append("remarques[]", val.note ? val.note : "");
      });
      return formData;
    },
    inverseObject(array) {
      let object = {};
      let days = array.map((val) => val.day);
      for (let d of days) {
        object[d] = array.filter((val) => val.day == d);
      }
      return object;
    },
    removeEvent() {
      this.selectedEtd = null;
      this.$refs.calendar.getApi().removeAllEvents();
    },
    updateEventValue() {
      console.log("update event");
      this.errors.meet_required = null;

      if (
        this.formData.is_remotely &&
        !this.formData.url_meet &&
        this.formData.meet_platform.id != "boti"
      ) {
        this.errors.meet_required =
          "Lien de la plateforme à distance est obligatoire.";
        return;
      }

      this.errors.date_end = null;
      if (
        new Date("1/1/1999 " + this.formData.start + ":00") >
        new Date("1/1/1999 " + this.formData.end + ":00")
      ) {
        this.errors.date_end =
          "Heure de fin de la séance doit etre supérieure à l'heure de début";
        return;
      }

      // update time
      let start = new Date(this.selectedEvent.start);
      let end = new Date(this.selectedEvent.end);

      let n_start = this.parseTime(start, this.formData.start);
      let n_end = this.parseTime(end, this.formData.end);

      // update
      this.selectedEvent.setDates(n_start, n_end);

      // update data
      let props = this.selectedEvent.extendedProps;
      for (let key in props) {
        this.selectedEvent.setExtendedProp(key, this.formData[key]);
      }
      this.chekEmpty(this.selectedEvent);
      this.$refs.eventModel.hide();
    },
    clearInputs() {
      return new Promise((resolve) => {
        // props
        let props = this.selectedEvent.extendedProps;
        console.log(props);
        // set time
        this.formData.start = this.parseDate(this.selectedEvent.start);
        this.formData.end = this.parseDate(this.selectedEvent.end);
        // set others
        for (let key in props) {
          console.log(key);
          this.formData[key] = props[key];
        }
        for (let key in this.errors) {
          this.errors[key] = null;
        }
        resolve();
      });
    },
    IsDateHasEvent(date) {
      var allEvents = [];
      allEvents = this.$refs.calendar.getApi().getEvents();
      // var event = $.grep(allEvents, function (v) {
      //   return +v.start === +date;
      // });
      let events = allEvents.filter(function (val) {
        return +val.start == +date;
      });
      return events.length > 0;
    },
    getItem(array, id) {
      return array.filter((item) => item.id == id)[0];
    },
    listEvents() {
      let allEvents = this.$refs.calendar.getApi().getEvents();
      console.log(allEvents);
      return this.eventstoArray(allEvents);
    },
    getData() {
      this.$refs.save.show();
      console.log(this.listEvents());
    },
    save() {
      this.$refs.confirmeSave.hide();
      this.$refs.save.show();
    },
    getTime(date) {
      return (
        date.getHours() + ":" + date.getMinutes() + ":" + date.getSeconds()
      );
    },
    getDate(date) {
      var d = new Date(date),
        month = "" + (d.getMonth() + 1),
        day = "" + d.getDate(),
        year = d.getFullYear();

      if (month.length < 2) month = "0" + month;
      if (day.length < 2) day = "0" + day;

      return [year, month, day].join("-");
    },
    newEdt() {
      this.selectedEtd = null;
      this.$refs.calendar.getApi().removeAllEvents();
    },
    getEvents(d) {
      this.$refs.calendar.getApi().removeAllEvents();
      this.selectedEtd = d;
      getSeance(d.id).then((res) => {
        res.data.forEach((val) => {
          let ev = val;
          this.$refs.calendar.getApi().addEvent(ev);
        });
      });
    },

    parseDate(date) {
      let strDate = new Date(date).toLocaleTimeString("en-US", {
        hour12: false,
      });
      return strDate.slice(0, strDate.lastIndexOf(":"));
    },
    parseTime(date, time) {
      let times = time.split(":");
      return new Date(
        date.getFullYear(),
        date.getMonth(),
        date.getDate(),
        times[0],
        times[1],
        0,
        0
      );
    },
    duplicate() {
      let events = this.$refs.calendar
        .getApi()
        .getEvents()
        .map((val) => ({
          title: val.title,
          start: val.start,
          end: val.end,
          color: "#fff",
          ...val.extendedProps,
        }));
      console.log(events);
      localStorage["events"] = JSON.stringify(events);
      localStorage["group"] = JSON.stringify(this.duplicateGroup);

      this.$refs.duplicate.hide();
      setTimeout(() => {
        window.open(location.href, "_blank");
      }, 1000);
    },
    chekEmpty(event) {
      event = this.serializEvent(event);
      this.showWarring = false;
      chekSeance({
        day: event.day,
        from: event.start,
        to: event.end,
        teacher: event.teacher ? event.teacher.id : null,
        unite: event.unite ? event.unite.id : null,
        matiere: event.matiere ? event.matiere.id : null,
        salle: event.salle ? event.salle.id : null,
        classe: this.slectedGroup.id,
      }).then((res) => {
        let data = res.data;
        console.log(data);
        this.warringMsg = data.warringMsg;
        this.warringData = data.warringData;
        this.showWarring = data.warring;
      });
    },
    printPdf() {
      var element = document.getElementById("calendar-container");
      var options = {
        filename: "test.pdf",
      };
      domToPdf(element, options, function () {
        console.log("done");
      });
    },
    duplicateEvent() {
      console.log(this.selectedEvent);
      let endDate = this.selectedEvent.end;
      endDate.setHours(endDate.getHours() + 1);
      this.$refs.calendar.getApi().addEvent({
        title: this.selectedEvent.title,
        start: this.selectedEvent.end,
        end: endDate,
        color: "#fff",
        ...this.selectedEvent.extendedProps,
      });
      this.$refs.eventModel.hide();
    },
    printElem() {
      var mywindow = window.open("", "PRINT", "height=400,width=600");
      mywindow.document.write("<html><head><title>G1-G3</title>");
      mywindow.document.write("</head><body >");
      mywindow.document.write("<h1>" + document.title + "</h1>");
      mywindow.document.write(this.$refs.calendar.$el.innerHTML);
      mywindow.document.write("</body></html>");

      mywindow.document.close(); // necessary for IE >= 10
      mywindow.focus(); // necessary for IE >= 10*/

      mywindow.print();
      return true;
    },
    init() {
      let events = JSON.parse(localStorage.getItem("events"));
      if (Array.isArray(events)) {
        console.log("ok");
        events.forEach((val) => {
          this.$refs.calendar.getApi().addEvent(val);
        });
        localStorage.removeItem("events");
      }
    },
    dateToStr(date) {
      date = new Date(date).toISOString();
      return date.slice(0, date.indexOf("T"));
    },
    setActivate(event, edt) {
      event.stopPropagation();
      setActivateEdt(edt.id).then((res) => {
        let data = res.data;
        this.$notify({
          group: "foo",
          type: "success",
          classes: ".noti",
          title: data.title,
          text: data.msg,
        });
      });

      this.listeEdts.forEach((edt) => {
        edt.active = 0;
      });
      edt.active = !edt.active;
    },
    initDrag() {
      setTimeout(() => {
        new Draggable(this.$refs.dragable_classes, {
          itemSelector: ".item_badge",
          eventData: (eventEl) => {
            return {
              title: eventEl.innerText,
              duration: "01:00:00",
              color: "#fff",
              serieID: null,
              // color: window.getComputedStyle(eventEl).backgroundColor,
              teacher: null,
              subject: null,
              matiere: null,
              class: this.classes[eventEl.getAttribute("data-class-index")],
              note: null,
              is_remotely: null,
              meet_platform: this.meets[0],
              url_meet: null,
            };
          },
        });

        new Draggable(this.$refs.dragable_teachers, {
          itemSelector: ".item_badge",
          eventData: (eventEl) => {
            return {
              title: eventEl.innerText,
              duration: "01:00:00",
              color: "#fff",
              // color: window.getComputedStyle(eventEl).backgroundColor,
              teacher:
                this.teachers[eventEl.getAttribute("data-teacher-index")],
              subject: null,
              matiere: null,
              serieID: null,
              class: null,
              note: null,
              is_remotely: null,
              meet_platform: this.meets[0],
              url_meet: null,
            };
          },
        });

        new Draggable(this.$refs.dragable_subjects, {
          itemSelector: ".item_badge",
          eventData: (eventEl) => {
            return {
              title: eventEl.innerText,
              duration: "01:00:00",
              color: "#fff",
              // color: window.getComputedStyle(eventEl).backgroundColor,
              serieID: null,
              teacher: null,
              subject:
                this.subjects[eventEl.getAttribute("data-subject-index")],
              matiere: null,
              class: null,
              note: null,
              is_remotely: null,
              meet_platform: this.meets[0],
              url_meet: null,
            };
          },
        });
      }, 1500);
    },
    createElement(field, value) {
      let input = document.createElement("input");
      input.setAttribute("type", "hidden");
      input.setAttribute("name", field);
      input.setAttribute("value", value);
      return input;
    },
    getParameterByName(name, url = window.location.href) {
      name = name.replace(/[[\]]/g, "\\$&");
      var regex = new RegExp("[?&]" + name + "(=([^&#]*)|&|#|$)"),
        results = regex.exec(url);
      if (!results) return null;
      if (!results[2]) return "";
      return decodeURIComponent(results[2].replace(/\+/g, " "));
    },
  },
};
</script>
