import Vue from 'vue'
import App from './App.vue'
import { BootstrapVue, IconsPlugin } from 'bootstrap-vue'
import axios from 'axios'
import VueAxios from 'vue-axios'
import Notifications from 'vue-notification'
import VueMask from 'v-mask'
axios.defaults.baseURL = document.querySelector("meta[name='baseapi']").getAttribute('content');

// axios
Vue.use(VueAxios, axios)
Vue.use(Notifications)
Vue.use(VueMask);

// vue select 
import vSelect from 'vue-select'
vSelect.props.components.default = () => ({
  Deselect: {
    render: createElement => createElement('span', ''),
  },
  OpenIndicator: {
    render: createElement => createElement('img', {
      attrs: {
        src: document.querySelector("meta[name='baseapi']").getAttribute('content') + 'assets/cours/emploi/imgs/dropdown.svg',
      },
      style: {
        cursor: 'pointer'
      }
    }),
  },
});

Vue.component('v-select', vSelect)
import 'vue-select/dist/vue-select.css';

// booostrap css
import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap-vue/dist/bootstrap-vue.css'
// tippy css
import 'tippy.js/dist/tippy.css';

// Install BootstrapVue
Vue.use(BootstrapVue)
// Optionally install the BootstrapVue icon components plugin
Vue.use(IconsPlugin)


Vue.config.productionTip = false
new Vue({
  render: h => h(App),
}).$mount('#app')
